.checkbox {
  position: relative;
  padding: 0.1rem;
  border-radius: 0.4rem;
  display: block;
  font-size: $font-size-list;

  input {
    position: absolute;
    opacity: 0;

    &:checked {
      & + label:before {
        background: $color-grey-dark;
        color: $color-main-light;
      }

      & + label:after {
        background: $color-grey;
        color: $color-main-darker;
        left: unset;
      }
    }
  }
}

.period {
  padding-bottom: 1rem;
  label {
    min-width: 21rem;
    cursor: pointer;
    display: block;
    &:before {
      content: 'All period';
      width: 45%;
      text-align: center;
      display: inline-block;
      padding: 1rem 0.5rem;
      transition: all 0.3s ease;
      background: $color-grey;
      box-shadow: inset 0 0 0.6rem black;
      left: 50%;
      bottom: 0;
      top: 0;
      border-radius: 0.4rem;
    }

    &:after {
      content: 'Today';
      width: 45%;
      display: inline-block;
      text-align: center;
      padding: 1rem 0.5rem;
      box-shadow: inset 0 0 0.6rem black;
      color: $color-main-light;
      bottom: 0;
      top: 0;
      border-radius: 0.4rem;
    }
  }

}

.population {
  label {
    min-width: 21rem;
    cursor: pointer;
    display: block;
    &:before {
      content: 'All population';
      min-width: 45%;
      text-align: center;
      padding: 1rem 0.5rem;
      display: inline-block;
      transition: all 0.3s ease;
      background: $color-grey;
      box-shadow: inset 0 0 0.6rem black;
      left: 50%;
      bottom: 0;
      top: 0;
      border-radius: 0.4rem;
    }

    &:after {
      content: 'Per 100 thou';
      min-width: 45%;
      text-align: center;
      display: inline-block;
      padding: 1rem 0.5rem;
      color: $color-main-light;
      box-shadow: inset 0 0 0.6rem black;
      bottom: 0;
      top: 0;
      border-radius: 0.4rem;
    }
  }
}


.btn-wrapper {
  display: flex;
  margin-left: 1rem;

  .btn-cases {
    box-shadow: inset 0 0 1rem black;
    padding: 0.5rem;
    z-index: 2;
    background-color: $color-grey-dark;
    cursor: pointer;

  }
  .active-btn {
    cursor: default;
    box-shadow: inset 0 0 0.2rem black;
  }
}

.full-screen {
  background: url("https://image.flaticon.com/icons/png/512/67/67760.png");
  background-color: $color-grey;;
  background-size: contain;
  width: 1.5rem;
  height: 1.5rem;
  position: absolute;
  top: 0;
  right: 0;
  cursor: pointer;
  z-index: 2;
}
.end-full-screen {
  background: url("https://image.flaticon.com/icons/png/512/18/18231.png");
  background-color: $color-grey;;
  background-size: contain;
  width: 1.5rem;
  height: 1.5rem;
  position: absolute;
  top: 0;
  right: 0;
  cursor: pointer;
  z-index: 2;
}
